
.team-container{
font-family: 'Poppins', sans-serif;
}

.team-title{
    padding: 200px 12% 50px 12%;
    font-size: 50px;
}

.team-member-container{
    padding:50px 12%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:5rem;
}

.join-team-container{
    background-color: #003F88;
    color:white;
    padding:100px 12%;
    margin-top:50px;
}

.join-team-title {
  font-size: 45px;
}

.join-team-info {
  padding-top: 30px;
  font-size: 30px;
}

.join-team-btn {
  border: 1px solid white;
  padding: 15px;
  width: 100px;
  text-align: center;
  border-radius: 15px;
  transition: 0.2s;
  margin-top: 50px;
  cursor: pointer;
}

.join-team-btn a {
  color: white;
  transition: 0.2s;
}

.join-team-btn:hover {
  border: 1px solid rgb(208, 208, 208);
}

.join-team-btn a:hover {
  color: rgb(208, 208, 208);
}

@media screen and (max-width: 1100px) {
  .team-member-container {
    padding: 50px 100px;
  }
}

@media screen and (max-width: 800px) {
  .join-team-title {
    font-size: 24px;
  }

  .join-team-info {
    font-size: 17px;
  }
}
